/* Timeline */
#agenda .date {
  text-align: center; }
  #agenda .date > label {
    font-size: 24px;
    padding: 10px 35px;
    color: white;
    font-weight: bold;
    width: 50%;
    background-color: rgba(44, 154, 184, 0.7);
    border-radius: 5px 0 0 5px; }
    #agenda .date > label:hover {
      background-color: #2c9ab8; }
    #agenda .date > label:last-of-type {
      background-color: rgba(184, 203, 94, 0.7);
      border-radius: 0 5px 5px 0; }
      #agenda .date > label:last-of-type:hover {
        background-color: #b8cb5e; }
    #agenda .date > label:before {
      display: none; }
  #agenda .date input[value='day-1']:checked + label {
    background-color: #2c9ab8;
    text-decoration: underline; }
  #agenda .date input[value='day-1']:checked ~ .day-1 {
    display: block; }
  #agenda .date input[value='day-1']:checked ~ .day-2 {
    display: none; }
  #agenda .date input[value='day-2']:checked + label {
    background-color: #b8cb5e;
    text-decoration: underline; }
  #agenda .date input[value='day-2']:checked ~ .day-2 {
    display: block; }
  #agenda .date input[value='day-2']:checked ~ .day-1 {
    display: none; }

#agenda .rooms {
  margin: 0 auto;
  max-width: 75%;
  text-align: left; }
  #agenda .rooms > .col-md-6 {
    padding-left: 40px; }
    #agenda .rooms > .col-md-6:last-of-type {
      padding-left: 0; }

#agenda .room {
  display: none;
  position: absolute;
  min-width: 50px;
  text-align: center;
  border-radius: 5px;
  color: white; }
  #agenda .room.room-101 {
    background-color: #f29c67; }
  #agenda .room.room-201 {
    background-color: #ff4242; }
  #agenda .room:before {
    content: attr(data-room); }

#agenda .navbar-mobile {
  position: fixed;
  right: 2px;
  bottom: 10px;
  display: none; }
  #agenda .navbar-mobile .back-top {
    text-decoration: none;
    display: block;
    width: 40px;
    height: 40px;
    text-align: center;
    line-height: 40px;
    border-radius: 50%;
    color: white; }
  #agenda .navbar-mobile .back-top {
    background-color: #bbb; }

#agenda .tag-groups {
  position: absolute;
  top: 35px; }

#agenda .event-level {
  font-size: 16px;
  padding: 0 10px; }
  #agenda .event-level.none, #agenda .event-level.beginner, #agenda .event-level.intermediate, #agenda .event-level.advanced {
    border-radius: 5px; }
  #agenda .event-level.none {
    background-color: #dddddd; }
  #agenda .event-level.beginner {
    background-color: #dff0d8; }
  #agenda .event-level.intermediate {
    background-color: #fcf8e3; }
  #agenda .event-level.advanced {
    background-color: #f2dede; }
  #agenda .event-level:before {
    content: attr(data-level);
    font-weight: 200; }

#agenda .timeline {
  border-left: 4px solid #2c9ab8;
  border-bottom-right-radius: 4px;
  border-top-right-radius: 4px;
  background: rgba(255, 255, 255, 0.03);
  color: #333;
  font-family: 'Source Sans Pro', sans-serif;
  margin: 0 auto 50px auto;
  letter-spacing: 0.5px;
  position: relative;
  line-height: 1.4em;
  font-size: 1.03em;
  padding: 30px;
  list-style: none;
  text-align: left;
  font-weight: 1;
  max-width: 75%; }
  #agenda .timeline h1 {
    font-family: 'Oswald', sans-serif;
    letter-spacing: 1.5px;
    font-weight: 100;
    font-size: 1.4em; }
  #agenda .timeline h2 {
    font-family: 'Oswald', sans-serif;
    letter-spacing: 1.5px;
    font-weight: 100;
    font-size: 1.4em; }
  #agenda .timeline h3 {
    font-family: 'Oswald', sans-serif;
    letter-spacing: 1.5px;
    font-weight: 100;
    font-size: 1.4em; }
  #agenda .timeline p {
    word-wrap: break-word; }
  #agenda .timeline .event {
    border-bottom: 1px dashed #2c9ab8;
    padding-bottom: 25px;
    margin-bottom: 50px;
    position: relative; }
    #agenda .timeline .event:last-of-type {
      padding-bottom: 0;
      margin-bottom: 0;
      border: none; }
    #agenda .timeline .event:before {
      position: absolute;
      display: block;
      top: 0;
      left: -217.5px;
      color: #717171;
      content: attr(data-date);
      text-align: right;
      font-weight: 400;
      font-size: 0.9em;
      min-width: 120px; }
    #agenda .timeline .event:after {
      position: absolute;
      display: block;
      top: 0;
      box-shadow: 0 0 0 4px #2c9ab8;
      left: -57.85px;
      background: #b8cb5e;
      border-radius: 50%;
      height: 11px;
      width: 11px;
      content: "";
      top: 5px; }

@media (max-width: 768px) {
  #agenda .date {
    padding: 0; }
    #agenda .date > label {
      font-size: 20px;
      width: 100%;
      border-radius: 0;
      margin-bottom: 0; }
      #agenda .date > label:last-of-type {
        border-radius: 0; }
    #agenda .date .day-1 h2, #agenda .date .day-2 h2 {
      margin-top: 20px;
      margin-bottom: -30px; }
  #agenda .rooms {
    display: none; }
  #agenda .room {
    display: block;
    top: -20px;
    left: -10px; }
  #agenda .navbar-mobile {
    display: block; }
  #agenda .timeline {
    margin: 50px auto; }
    #agenda .timeline h3 {
      min-width: 95px;
      margin-left: -25px;
      margin-top: 25px;
      border-bottom: none; }
    #agenda .timeline p {
      margin-left: -25px; }
    #agenda .timeline .event:before {
      left: -31.5px;
      top: -11px;
      background-color: #2c9ab8;
      color: white;
      margin-top: 8px;
      padding: 2px 8px 2px 8px;
      content: attr(data-date);
      text-align: right;
      font-weight: 400;
      font-size: 0.9em;
      min-width: 130px;
      border-radius: 0 5px 5px 0; }
    #agenda .timeline .event .col-md-6:first-of-type {
      border-bottom: 1px solid rgba(44, 154, 184, 0.3);
      margin-top: 65px; }
    #agenda .timeline .event .col-md-6 h3 {
      margin-top: 45px;
      margin-bottom: 0; }
    #agenda .timeline .event p {
      top: 27px;
      padding: 10px 0px 10px 0px;
      position: relative;
      width: 10rem; }
    #agenda .timeline .tag-groups {
      position: absolute;
      top: -20px;
      left: 50px; }
      #agenda .timeline .tag-groups .event-level {
        min-width: 50px;
        text-align: center;
        margin-right: 2px; } }
